.gpt3__blog {
  display: flex;
  flex-direction: column;
}
.gpt3__blog-heading {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}
.gpt3__blog-heading h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 62px;
  line-height: 85px;
  /* or 121% */

  letter-spacing: -0.04em;

  color: var(--gardient__text);
  margin-top: 5rem;
}

.gpt3__blog-container {
  display: flex;
  flex-direction: row;
  margin-top: 5rem;
}
.gpt3__blog-container_groupeA {
  flex: 0.75;
  margin-right: 2rem;
}

.gpt3__blog-container_groupeB {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
@media screen and (max-width: 990px) {
  .gpt3__blog-container {
    flex-direction: column-reverse;
  }
  .gpt3__blog-container_groupeA {
    margin: 2rem 0;
  }

  .gpt3__blog-container_groupeA .gpt3__blog-container_article {
    width: 48%;
  }
  .gpt3__blog-container_groupeA .gpt3__blog-container_article-image {
    height: 250px;
  }
}
@media screen and (max-width: 700px) {
  .gpt3__blog-container_groupeB {
    grid-template-columns: repeat(1, 1fr);
  }
  .gpt3__blog-container_groupeA .gpt3__blog-container_article {
    width: 100%;
  }
  .gpt3__blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
  }
}
